  background-attachment: fixed;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  .about_block {
    width: 60%;
    // width: auto;
    background-color: #323239;
    padding: 20px;
    border-radius: 8px;

    .about_info {
      h1 {
        color: #f6f9f9;
      }

      p {
        color: #7d7e82;
        line-height: 30px;
        padding: 30px 0;
      }
    }

    .about_contacts {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      flex-wrap: wrap;
      color: #fdb2b7;

      p{
        padding: 10px 0;
      }

      p svg {
        padding: 0 5px;
      }
    }
  }
}

@media (max-width: 992px) {
  .about .about_block {
    width: 80%;
  }
}

@media (max-width: 576px) {
    .about .about_block {
        width: 95%;
      }
}
